import React, { useState, useEffect } from "react";
import axios from "axios";
import styles from './SeeReservation.module.css';

function SeeReservation() {
    const [reservations, setReservations] = useState([]);
    const [error, setError] = useState(null);
    const [showModal, setShowModal] = useState(false);
    const [modalMessage, setModalMessage] = useState("");
    const [selectedId, setSelectedId] = useState(null); //   ID    
    const [loading, setLoading] = useState(false); //   

    const handleCancelReservation = (id) => {
        const token = localStorage.getItem('token');
        if (!token) {
            setError('User is not authenticated');
            return;
        }

        setSelectedId(id);
        setModalMessage(" ?");
        setShowModal(true);
    };

    const handleConfirm = async () => {
        setLoading(true); //  
        try {
            const token = localStorage.getItem('token');
            await axios.delete(`http://192.168.100.38:3000/reservation/delete/${selectedId}`, {
                headers: { 'authorization': token }
            });

            setReservations(reservations => reservations.filter(reservation => reservation.id !== selectedId));

        } catch (err) {
            console.error(err);
            setError('Error cancelling the reservation');
        } finally {
            setLoading(false); //  
        }
    }

    const handleCancel = () => {
        setSelectedId(null);
        setShowModal(false);
    }

    useEffect(() => {
        const fetchReservations = async () => {
            setLoading(true); //  
            const token = localStorage.getItem('token'); 
            if(!token) {
                setError('User is not authenticated');
                return;
            }

            try {
                const response = await axios.get('http://192.168.100.38:3000/user/reservation', {
                    headers: { 'authorization': token }
                });
                
                setReservations(response.data);
            } catch (err) {
                console.error(err);
                setError('Error fetching reservation data');
            } finally {
                setLoading(false); //  
            }
        };

        fetchReservations();
    }, []);
    
    return (
        <div className={styles.container}>
            <p></p>
            {  loading ? (
                    <p>Loading...</p>
                ) : reservations.length > 0 ? (
                    reservations.map((reservation, index) => (
                    <div key={index} className={styles.reservationItem}>
                        {/* <p>: {reservation.id}</p> */}
                        <p>: {reservation.lat}</p>
                        <p>: {reservation.lng}</p>
                        <p> : {new Date(reservation.reservation_time).toLocaleString()}</p>
                        <p> : {new Date(reservation.return_time).toLocaleString()}</p>
                        {/* <p> ID: {reservation.user_id}</p> */}
                        {/* <p> ID: {reservation.vehicle_id}</p> */}
                        <p> :{reservation.price}</p>
                        <p> : {reservation.car_info[0].car_number}</p>
                        <p>  :</p>
                        <button onClick={() => handleCancelReservation(reservation.id)} className={styles.cancelButton}> </button>
                    </div>
                    
                ))
            ) : (
                <p> </p>
            )}
            
            {showModal && (
                <div className={styles.modal}>
                    <div className={styles.modalContent}>
                        <span className={styles.closeButton} onClick={handleCancel}>X</span>
                        {modalMessage}
                        <button onClick={handleConfirm}></button>
                        <button onClick={handleCancel}></button>
                    </div>
                </div>
            )}
        </div>
    )
}

export default SeeReservation;
