import React, { useState } from 'react';
import styles from './Signup.module.css';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Signup() {
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [phonenumber, setPhonenumber] = useState('');
  const [message, setMessage] = useState('');
  const [isIdAvailable, setIsIdAvailable] = useState(false); //    

  const navigate = useNavigate();

  const idCheck = async () => {
    if(!email) {
      setMessage(' .');
      return;
    }
    
    try {
      const response = await axios.post('http://192.168.100.38:3000/user/idcheck', { email });
      if (response.status === 205) {
        setMessage('  .');
        setIsIdAvailable(false);
      } else {
        setMessage('  .');
        setIsIdAvailable(true);
      }
    } catch (error) {
      console.error('ID Check Error:', error);
      setMessage('    .');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if(!isIdAvailable) return; //         

    const userData = {
      email,
      name,
      password,
      phonenumber,
    };

    try {
      const response = await axios.post('http://192.168.100.38:3000/user/signup', userData);
      
      if (response.status === 200) {
        alert(' ');
        navigate('/login');
      } else {
        setMessage('   ');
      }
    } catch (error) {
      console.error('Error:', error);
      setMessage('   ');
    }
  };

  return (
    <div className={styles.signupContainer}>
      <form onSubmit={handleSubmit} className={styles.signupForm}>
        <h2>Signup</h2>
        {message && <p>{message}</p>}
        <div className={styles.inputField}>
          <label>Email</label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <button type="button" onClick={idCheck}> </button>
        </div>
        <div className={styles.inputField}>
          <label>Name</label>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className={styles.inputField}>
          <label>Password</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className={styles.inputField}>
          <label>Phone Number</label>
          <input
            type="tel"
            value={phonenumber}
            onChange={(e) => setPhonenumber(e.target.value)}
            pattern="\d*"
            title="Please enter numbers only"
            required
          />
        </div>

        <button type="submit">Signup</button>
      </form>
    </div>
  );
}

export default Signup;
