import React, { useState } from 'react';
import styles from './Login.module.css';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
// import {Url} from '../server_url';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [modalMessage, setModalMessage] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const navigate = useNavigate();
  // const url = Url
  

  const handleSubmit = async (e) => {
    e.preventDefault();

    const userData = {
      email,
      password,
    };

    try { //   Localhost  
      // const response = await axios.post(`${url}user/login`, userData);
      const response = await axios.post('http://192.168.100.38:3000/user/login', userData);

      if (response.status === 200) {
        const { token, name, id } = response.data;
        localStorage.setItem('token', token);
        localStorage.setItem('name', name);
        localStorage.setItem('userId', id);
        navigate('/');
        window.location.reload();
      } else {
        handleModal(response.status);
      }

    } catch (error) {
      console.error('Error:', error);
      setErrorMessage('   ');
    }
  };

  const handleModal = (status) => {
    if (status === 202) {
      setModalMessage('   ');
    } else if (status === 205) {
      setModalMessage(' ');
    } else {
      setModalMessage('   ');
    }
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setModalMessage('');
  };

  return (
    <div className={styles.loginContainer}>
      <form onSubmit={handleSubmit} className={styles.loginForm}>
        <h2>Login</h2>
        {errorMessage && <p>{errorMessage}</p>}
        <div className={styles.inputField}>
          <label>Email</label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className={styles.inputField}>
          <label>Password</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit">Login</button>
        <p style={{ textAlign: 'center', marginTop: '10px' }}>
          <a href="/password-reset" style={{ color: '#800080' }}> ?</a>
        </p>
      </form>

      {isModalOpen && (
        <div style={modalStyles}>
          <div style={modalContentStyles}>
            <p>{modalMessage}</p>
            <button onClick={closeModal}>Close</button>
          </div>
        </div>
      )}
    </div>
  );
}

const modalStyles = {
  position: 'fixed',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: 'rgba(0, 0, 0, 0.5)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
};

const modalContentStyles = {
  backgroundColor: '#fff',
  padding: '20px',
  borderRadius: '5px',
};

export default Login;


