import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './Menu.module.css'; 
import axios from 'axios'; // axios import.


function Menu() {
  const navigate = useNavigate();
  const [token, setToken] = useState(null);
  const [name, setName] = useState(null); //     .
  const [id, setId] = useState(null);

  const [isModalOpen, setIsModalOpen] = useState(false); // modal
  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };
  
  useEffect(() => {
    const storedToken = localStorage.getItem('token');
    const storedName = localStorage.getItem('name'); //    .
    const storedId = localStorage.getItem('id');
    setToken(storedToken);
    setName(storedName); //    .
    setId(storedId);

    console.log(storedToken);
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('name'); 
    localStorage.removeItem('userId');
    setToken(null);
    setName(null); //   .
    navigate('/');
    window.location.reload(); //  .
  };
  const confirmDeleteAccount = async () => {
    try {
      const response = await axios.delete('http://192.168.100.38:3000/user/delete', {
        headers: {
          'authorization': localStorage.getItem('token')
        }
      });
      if(response.status === 200) {
        alert('  .');
        handleLogout();
      } else {
        alert('    .  .');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('    .  .');
    }
  };

  const goToLogin = () => {
    navigate('/login');
  };

  const goToSignup = () => {
    navigate('/signup');
  };

  const goToChangeinfo = () => {
    navigate('/changeinfo');
  };

  const goToSeeReservation = () => {
    navigate('/seereservation');
  };



  return (
    <div className={styles.container}>
      <h1></h1>
      {name && <p>! {name}!</p>} {/*     . */}
      {id && <p>ID: {id}</p>}
      {!token && <button className={styles.button} onClick={goToLogin}></button>}
      {!token && <button className={styles.button} onClick={goToSignup}></button>}
      {token && <button className={styles.button} onClick={handleLogout}></button>}
      {token && <button className={styles.button} onClick={goToChangeinfo}></button>}
      {token && <button className={styles.button} onClick={goToSeeReservation}></button>}
      {token && <button className={`${styles.button} ${styles.deleteButton}`} onClick={openModal}> </button>}

      {isModalOpen && (
        <div className={styles.modal}>
          <div className={styles.modalContent}>
            <p>   ?</p>
            <button onClick={confirmDeleteAccount}></button>
            <button onClick={closeModal}></button>
          </div>
        </div>
      )}
    </div>
  );
}


export default Menu;
