import React from "react";
import SeoulImage from '../assets/Seoul.png'

function Seoul() {
  const dummyData = [
    { id: 1, title: "", image: SeoulImage },
    { id: 2, title: " ", image: SeoulImage },
    { id: 3, title: "", image: SeoulImage },
    { id: 4, title: "  ", image: SeoulImage },
    { id: 5, title: "", image: SeoulImage },
    { id: 6, title: "", image: SeoulImage },
    { id: 7, title: "", image: SeoulImage },
    { id: 8, title: "", image: SeoulImage },
    { id: 9, title: "", image: SeoulImage },
    { id: 10, title: "", image: SeoulImage }
  ];

  return (
    <div style={{ marginBottom: '70px' }}>
      <p></p>
      <div style={{ display: 'flex', flexWrap: 'wrap', gap: '20px' }}>
        {dummyData.map(item => (
          <div key={item.id} style={{ border: '1px solid #ccc', borderRadius: '5px', padding: '10px', width: '150px' }}>
            <img src={item.image} alt={item.title} style={{ width: '100%', height: '100px', objectFit: 'cover' }} />
            <p style={{ textAlign: 'center', margin: '10px 0 0 0' }}>{item.title}</p>
          </div>
        ))}
      </div>
    </div>
  );
}


export default Seoul;